body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
.app-root-container {
    font-family: sans-serif;
}

.mail-form-container {
    height: calc(100vh - 64px);
    background: #F0F0F0;
}
.landing-splash-container {
    min-width: 600px;
    margin-top: 64px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
    min-height: calc(100vh - 64px);
    background: -webkit-linear-gradient(left, #1d1133, #353535);
    background: linear-gradient(90deg, #1d1133, #353535);
}

.intro-image {
    -webkit-align-self: center;
            align-self: center;
    -webkit-flex-basis: 30%;
            flex-basis: 30%;
    margin-top: 10vh;
    margin-bottom: 10vh;
}

.intro-text {
    color: white;
    white-space: nowrap;
    min-width: 200px;
    -webkit-align-self: center;
            align-self: center;
    -webkit-flex-basis: 30%;
            flex-basis: 30%;
    font-family: sans-serif;
    margin-bottom: 10vh;
}

.intro-text-title {
    margin-bottom: 1px;
    font-size: 48px;
}

.intro-text-subtitle {
    margin-top: 1px;
    margin-left: 10px;
    font-size: 14px;
    margin-bottom: 48px;
    font-weight: 100;
    color: #ffffffaa
}

.intro-text-content {
    font-weight: 400;
}

.intro-text-content > li {
    margin-top: 10px;
}
.header-container {
    height: 64px;
    width: 100vw;
    position: fixed;
    background-color: #212121;
    border-bottom: 1px solid rgba(255, 255, 255, 0.158);
    top: 0;
    z-index: 2;
    grid-template-columns: auto 200px 32px;
    grid-template-areas: ". nav .";
}
.nav-flex-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    width: 95%;
    padding-right: 10%;
}

.nav-item {
    -webkit-justify-content: center;
            justify-content: center;
    color: white;
    font-family: 'Poiret One', cursive;
    font-size: 16px;
    margin-top: 42px;
    margin-right: 16px;
    margin-left: 16px;
    margin-bottom: 20px;
    cursor: pointer;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
}
.experience-splash-container {
    background: #F0F0F0;
    padding: 2%;
}
.experience-block-container {
    background: white;
    margin-top: 0%;
    margin-bottom: 2%;
    margin-left: 2%;
    margin-right: 2%;
    box-shadow: 4px 4px #EEEEEE;
    min-width: 650px;
}

.experience-block-image {
    width: 15vw;
    min-width: 180px;
    height: 120px;
}

.experience-block-container .company-name {
    font-size: 45px;
    font-family: 'Poiret One', cursive;
    font-weight: 800;
}

.experience-block-container .company-description {
    color: #666666;
    margin-top: 12px;
}

.experience-block-header {
    border-bottom: 1px solid #bbbbbb;
    padding-left: 36px;
    padding-right: 36px;
    padding-top: 24px;
    padding-bottom: 24px;
}

.experience-details-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: auto;
            justify-content: auto;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding-left: 24px;
    padding-top: 12px;
    padding-bottom: 12px;
}

.solution-container {
    margin-right: 24px;
    -webkit-align-self: flex-start;
            align-self: flex-start;
    margin-left: 40px;
}

.problem-container {
    -webkit-align-self: flex-start;
            align-self: flex-start;
    min-width: 620px;
}

.solution-label {
    font-weight: 200;
    color: #444444;
}

.problem-label {
    font-weight: 200;
    color: #444444;
}

.challenge-item {
    margin-top: 24px;
}
.education-splash-container {
    background: #212121;
    padding: 64px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.education-text-container {
    -webkit-align-self: center;
            align-self: center;
}

.education-title {
    color: white;
    font-family: 'Poiret One', cursive;
    font-size: 32px;
}

.education-details {
    margin-top: 4px;
    color: #cccccc;
}

.course-list-container {
    margin-top: 24px;
}

.course-list {
    margin-top: 6px;
}

.course-list > li {
    margin-top: 10px;
}
